import React, { Component } from 'react';
import AC from '../../copos/loading/ac';
// import Timer from '../../copos/time/timer';
import './index.less';

class Loading extends Component {


    render() {
        return (
            <div className="content">
                <AC />
                {/* <Timer /> */}
                <h5>页面正在加载中,请稍等……</h5>
            </div>
        )
    }
}

export default Loading;


