import React, { Component } from 'react';
import LoadingImg from './img/ac.gif';
import './index.less';

class Loading extends Component {
    render() {
        return (
            <img className="loading-img" src={LoadingImg} alt="loading images" />
        )
    }
}

export default Loading;


